const SpeciesRow = ({db, ps, n, parms}) => (
  <tr>
    <td>
      <select {...ps('species' + n)}>
        <option/>
        {Object.keys(db.seedList).map(s => <option key={s}>{s}</option>)}
      </select>
    </td>
    <td><input {...ps('rate' + n)} /> </td>
    <td><input {...ps('price' + n)} /> </td>
    <td>{(db.seedList[parms['species' + n]] || {}).seedingRate}</td>
    <td>{(db.seedList[parms['species' + n]] || {}).price}</td>
  </tr>
) // SpeciesRow
  
const Species = ({setScreen, db, parms, ps}) => (
  <div className="Species">
    <h1 id="H1">Economic Decision Aid for Cover Crops: Species Selection</h1>

    <div id="About">
      Species selection and planting rate are important decisions for successful cover cropping experience.
      You can access several selection tools by clicking on the links below.
      The information in the green cells are guidelines for the species you selected.
      You can use them or change them to make this analysis specific to your farm.
    </div>

    <table>
      <thead>
        <tr>
          <th colSpan="3">User Input</th>
          <th colSpan="2">Common rates and prices</th>
        </tr>
        <tr>
          <th>Cover Crop Species</th>
          <th>Seeding Rate<br/>(lb/ac)</th>
          <th>Price<br/>($/lb)</th>
          <th>Seeding Rate<br/>(lb/ac)</th>
          <th>Price<br/>($/lb)</th>
        </tr>
      </thead>
      <tbody>
        <SpeciesRow ps={ps} db={db} parms={parms} n='1'/>
        <SpeciesRow ps={ps} db={db} parms={parms} n='2'/>
        <SpeciesRow ps={ps} db={db} parms={parms} n='3'/>
        <SpeciesRow ps={ps} db={db} parms={parms} n='4'/>
        <SpeciesRow ps={ps} db={db} parms={parms} n='5'/>
        <tr>
          <td colSpan="2">Cost of Cover crop seed ($/acre)</td>
          <td>{parms.rate1 * parms.price1 + parms.rate2 * parms.price2 + parms.rate3 * parms.price3 + parms.rate4 * parms.price4 + parms.rate5 * parms.price5}</td>
        </tr>
      </tbody>
    </table>

    <div>
      <button onClick={() => setScreen('Planting') }>Next</button>
    </div>
  </div>
) // Species

Species.menu = 'Cover Crop Selection';

export default Species;